  .input-group {
    display: flex;
    gap: 10px;
  }
  
  .button-group {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .app-container {
    position: relative;
  }
  
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal {
    background: #fff;
    padding: 20px;
    border-radius: 5px;
    animation: slideIn 0.3s ease-out;
  }
  
  @keyframes slideIn {
    from {
      transform: translateY(100%);
    }
    to {
      transform: translateY(0);
    }
  }
  
  .input-group {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  
  .label {
    display: flex;
    align-items: center; /* Center labels vertically */
  
  }
  
  /* Adjusted styles for input fields */
  input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .button-group {
    display: flex;
    justify-content: space-between;
  }
  
  button {
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .tick {
    margin-left: 5px;
  }
